.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

.blog {
    padding-top: 10px;
    padding-left: 2px;
    border-style: solid;
    border-width: 1px;
    margin-bottom: 5px;
}

.success {
    color: rgb(15, 137, 15);
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

p {
    margin: 0;
    padding: 0;
}

.remove-button {
    background-color: #0663ef;
    border-radius: 5px;
}

.remove-button:hover {
    background-color: #4386eb;
}

.navigation-pane {
    background-color: #d3d3d3;
    display: flex;
    list-style: none;
    gap: 10px;
    margin: 0;
    padding: 5px;
}
